.main {
  max-width: 1280px;
  display: flex;
  flex-direction: column;
  margin: auto;
}

.sections {
  display: flex;
  gap: 60px;
}

.section {
  max-height: calc(100vh - 180px);
  overflow-y: scroll;
  overflow-x: hidden;
  max-width: 608px;
}

.board {
  grid-column-start: 2;
  grid-row-start: 2;
}

.stats {
  display: flex;
  gap: 36px;
}

.status {
  flex: 1 0;
}

.statusReady {
  color: #00cccc;
}

.numbers {
  display: flex;
  flex-direction: column;
  gap: 8px;
  flex-wrap: wrap;
  max-height: 313px;
}

.shadow {
  text-shadow: 0 0 16px rgba(51, 51, 255, 0.25), 0 0 8px rgba(51, 51, 255, 0.25),
    0 4px 32px rgba(51, 51, 255, 0.5);
}
