.card {
  display: flex;
  flex-direction: column;
  background-color: var(--background);
  border-radius: 40px;
  margin-bottom: 16px;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.cost {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.link {
  text-decoration: none;
  color: var(--text-primary-color);
}

.done {
  color: #00cccc;
}
